import { defineFeature, loadFeature } from "jest-cucumber";
import { fireEvent, render, waitFor } from "@testing-library/react";
import Base from "../../Base";
import React from "react";
import "text-encoding";
import parkingSlice, {
  initializeParkingLot,
} from "../../redux/reducer/parkingSlice";
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { Link } from "react-router-dom";
const feature = loadFeature("src/components/features/base.feature");
let store: any;
let rendered: any;
defineFeature(feature, (test) => {
  test("User enters the number of spaces", ({ given, when, then, and }) => {
    store = configureStore({
      reducer: {
        parkingSpaces: parkingSlice,
      },
    });
    given("the user is on the home page", () => {
      const screen = render(
        <Provider store={store}>
          <Router>
            <Base />
          </Router>
        </Provider>
      );
      expect(screen).toBeTruthy();
    });
    when("the user enters some input in the input field and submits", () => {
      const screen = render(
        <Provider store={store}>
          <Router>
            <Base />
          </Router>
        </Provider>
      );
      const input = screen.getAllByTestId("input");
      const button = screen.getAllByText("Submit");
      fireEvent.change(input[0], { target: { value: 10 } });
      fireEvent.click(button[0]);
    });
    then('an alert is displayed with "10 Spaces selected"', () => {
      const screen = render(
        <Provider store={store}>
          <Router>
            <Base />
          </Router>
        </Provider>
      );
      const input = screen.getAllByTestId("input");
      const button = screen.getAllByText("Submit");
      fireEvent.change(input[0], { target: { value: 10 } });
      fireEvent.click(button[0]);
      expect(window.alert).toHaveBeenCalledWith("10 Spaces selected");
    });
    and("the parking lot should be initialized with 10 spaces", () => {
      const screen = render(
        <Provider store={store}>
          <Router>
            <Base />
          </Router>
        </Provider>
      );
      const input = screen.getAllByTestId("input");
      const button = screen.getAllByText("Submit");
      fireEvent.change(input[0], { target: { value: 10 } });
      fireEvent.click(button[0]);
      expect(store.getState().parkingSpaces.numSpaces).toEqual(10);
    });
  });
});
